:root {
  --primary-color: #2ec4b6;
  --secondary-color: #C0F0EC;
  --third-color: #fff;
  --fourth-color: rgba(0,0,0,0.7);
  --fb-color: rgb(145, 145, 255);
  --instagram-color: rgb(255, 175, 70);
  --whatsapp-color: rgb(0, 128, 0);
  --twitter-color: rgb(57, 199, 163);
  --light-color: rgba(0, 213, 255,1);
  --dark-color: rgba(3,3,94,1)
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body{
    position: relative;
    overflow-x: hidden;

}
.body{
  width:calc(100vw-230px);
    margin-left: 78px;
    min-height: 60vh;
    position: relative;
}



/* Logo CSS */
.st0{
    fill:#3FA9F5;
}
.st1{
    fill:#3FA9F5;
    stroke:#3FA9F5;
    stroke-width:10;
    stroke-miterlimit:10;
}
.st2{
    fill:#FFFFFF;
}
.st3{
    fill:#878787;
}
.logo{
    transition: all ease-in-out 0.3s;
    /* z-index: 10; */
}

@media screen and (max-width:480px) {
  .logo{
    width: 150px !important;
  }
  
}

@media screen and (max-width:480px) {
    
  .body{
    width:100vw;
    margin: 0;
    min-height: 80vh;
    
  }
    
}
.header{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 75px;
    transition: all ease-in-out 2s;
    background-color: #fff;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 10;
    /* margin-bottom: 10px; */
    /* box-shadow: 0 0 10px 1px rgb(185, 185, 185); */
    position: relative;
}

.hamburger{
    position:absolute;
    left:20px;
    top:10px;
    transition: all ease-in-out 0.3s;
    z-index: 10;
}

.toggleNavbar{
    transform: translateX(300px);
    
}

.divider{
  height:5px;
position: relative;
  background: rgb(3,3,94);
  margin-bottom: 20px;
  width: 100%;
}
.divider-submenu{
  position: absolute;
  top:0;
  /* left: 0; */
  height: 5px;
  width:25%;
  background: linear-gradient(90deg, var(--dark-color) 0%, var(--light-color) 50%, var(--dark-color) 100%);
  animation: colorRainbow 4s ease-in-out  0s infinite ;
}
@keyframes colorRainbow{
  0%{
    left:0%;
  }
  50%{
    left:80%;
  }
  100%{
    left:0%;
  }
}


