.search{
  display: flex;
  justify-content: center;
  position: relative;
  
}
.Search-Box{
width:60%;
background-color: #C0F0EC;
display: flex;
flex-direction: column;
/* justify-content: center; */
align-items: center;
padding: 30px;
border-radius: 10px;
z-index: 1;
}

.search .bx{
  color:var(--primary-color);
  font-size: 50px;
  padding: 10px;
}

.search .bx{
  transition: all ease-in-out 0.3s;
  cursor: pointer;
}

.Search-Box .Price-Select{
  display: flex;
  flex-direction: row;
  width: 100%;
  /* align-items: center; */
  /* justify-content: center; */
}

@media screen and (max-width:480px) {
  .Search-Box{
    width: 90%;
  }
  .Search-Box .Price-Select{
    flex-direction: column;
  }
  .Search-Box .Price-Select-inputs{
    width: 100%;
  }
}

.Price-Select p{
  padding: 10px;
}

.Search-Box .Price-Select-inputs{
  width: 100%;
}

.Search-Box .Advance-Search{
  width: 100%;
  display: grid;
  grid-template-columns: 40% 60%;
  grid-template-rows: auto ;
  column-gap: 3px;
}

.Search-Box .Advance-Search>.Multi-Select{
width: 100%;
margin-top: 5px;
}

.Search-Box .Advance-Search>p{
  margin-top: 10px;
}

