.rotate180{
    transform: rotate(180deg);
}

.rotate0{
    transform: rotate(0deg);
}

#visual{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    
}

.Search-Parent{
    width: 100%;
    min-height:50em;
    
}

.plx-block-1, .plx-block-2{
    position: fixed;
    top: 300px;
    height: 100%;
    width:250px;
    margin: 0px 30px;
    z-index: 1;
}
.plx-block-2{
    right:0;
}
.parallax-image{
    position: absolute;
    width:100%;
}
.plx-1{
    margin-top: 100px;
    z-index: 2;
    
}
.plx-2{
    top: 610px;
z-index: 1;
}
.plx-3{
    z-index: 1;
    top: 490px;
    margin-right: 20px;
}
.plx-4{
z-index: 2;

}

.plx-4, .plx-3{
    right:0px
}

.plx-1, .plx-4{
    transition: all ease-in-out 0.3s;
    animation: upDown 5s infinite 0s ;
    
}

@keyframes upDown {
    0% {top:0px;}
    50% {top:-30px;}
    100% {top:0px;}
    
}

.entrance-left{
    animation: entrance-left 2s ease-in-out 0s;
    animation-iteration-count: 1;
    
}

@keyframes entrance-left {
    0%{
        transform: translate3d(-400px,-200px,-200px)
    }
    50%{
        transform: translate3d(200px,-100px,100px)
    }
    100%{
        transform: translate3d(0,0,0);
    }
}

.entrance-right{
    animation: entrance-right 2s ease-in-out 0s;
    animation-iteration-count: 1;
    
}

@keyframes entrance-right {
    0%{
        transform: translate3d(400px,200px,200px)
    }
    50%{
        transform: translate3d(-200px,-100px,-100px)
    }
    100%{
        transform: translate3d(0,0,0);
    }
}
